import {observer} from "mobx-react";
import {cards} from "../init";

const GroupCard = observer(function GroupCard(props) {

    const {show, store, group, header, id} = props;
    const clickAddCard = (card) => store.addCard(card);
    const classN = "accordion-collapse collapse " + (show ? "show" : "");
    const classB = "accordion-button " + (show ? "" : "collapsed");

    return <div className="accordion-item">
        <h2 className="accordion-header" id={group}>
            <button className={classB} type="button" data-bs-toggle="collapse"
                    data-bs-target={"#" + group + id} aria-expanded="false" aria-controls={group}>
                {header}
            </button>
        </h2>
        <div id={group + id} className={classN} aria-labelledby={group}
             data-bs-parent="#accordionExample">

            {cards.listFilter(group, store.searchText).map(c => {
                return <div className={"row"} key={c.src}>
                    <div className={"col-8"}>
                        <img src={c.src} className="card-img-top" alt={""}
                             onClick={() => clickAddCard(c)} style={{width: "50%"}}/>
                    </div>
                    <div className={"col align-self-center f-strong"}>
                        {c.header}
                    </div>
                </div>
            })}

        </div>
    </div>;
});

function ListCard(props) {

    return <div className="accordion" id="accordionExample">
        {props.store.listGroup.map(g => {
            return <GroupCard id={props.id} store={props.store} group={g.title} header={g.header}
                              key={g.title}
                              show={g.title === "action"}/>
        })}
    </div>;
}

export default observer(ListCard);