import {computed, makeObservable, observable} from "mobx";
import want from "../image/want.jpg";
import not_want from "../image/not_want.jpg";
import apple from "../image/apple.jpg";

export default class Cards {

    _listCard = [];

    constructor() {
        makeObservable(this, {
            _listCard: observable,
            listCard: computed
        });

        let c = {header: "", title: " - ", src: want, group: "action"};
        this._listCard.push(c);

        c = {header: " ", title: " -  ", src: not_want, group: "action"};
        this._listCard.push(c);

        c = {header: "", title: " - ", src: apple, group: "item"};
        this._listCard.push(c);
    }

    get listCard() {
        return this._listCard;
    }

    listFilter = (group, searchText) => {
        if (!group && !searchText)
            return this.listCard;

        if (group && !searchText)
            return this.listCard.filter(c => c.group === group);

        const t = searchText.toLowerCase();
        return this.listCard
            .filter(c => c.group === group)
            .filter(c => {
                return c.header.toLowerCase().includes(t)
                    || c.title.toLowerCase().includes(t)
                    || c.src.toLowerCase().includes(t)
                    || c.group.toLowerCase().includes(t)
            });
    }
}